import { Container, Grid } from '@mui/material';
import React from 'react';
import AdvantagesGridItem from './AdvantagesGridItem';

export default function AdvantagesBlock() {
  const gridContent = [
    {
      text: `     .
         30   20 .
       ,  ,
         
           .`,
      image: 'book',
      heading: '',
    },
    {
      text: `    ,
      ,     ,   
        . `,
      image: 'dictionary',
      heading: '',
    },
    {
      text: `     ,
        2 :   
      ,   
            .`,
      image: 'game',
      heading: '',
    },
    {
      text: `     
      ,     ,  
          .
          ,   `,
      image: 'stat',
      heading: '',
    },

  ];

  return (
    <Container maxWidth="xl" className="main__advantages">
      <Grid
        container
        sx={{
          marginTop: '60px',
        }}
      >
        <Grid
          item
          xs={12}
          sx={{
            color: 'white',
            fontSize: '64px',
            textAlign: 'center',
          }}
        >
           
        </Grid>
        {
          gridContent.map((item) => (
            <AdvantagesGridItem
              text={item.text}
              image={item.image}
              heading={item.heading}
              key={item.text.length}
            />
          ))
        }
      </Grid>
    </Container>
  );
}
